* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Reem Kufi", sans-serif !important;
}

:root {
  --color-primario: #fff;
  --color-text: #5a5a5a;
  --color-text-light: #f6f6f6;
  --color-secundario: #fbae08;
  --color-fondo: #dc0404;
  --color-negro: #111111;
}

img {
  max-width: 100%;
  height: auto;
}
.App {
  min-height: 100vh;
  width: 85vw;
  max-width: 1900px;
  margin: 0rem auto;
}
body {
  max-width: 100%;
  overflow-x: hidden;
}

body {
  background-color: #f6f6f6;
}

.nav-logo {
  max-width: 80px;
  border-radius: 10px;
}

nav {
  background-color: var(--color-fondo);
  min-height: 90px;
  width: 100%;
  display: flex;
  justify-content: center;
  position: fixed;
  z-index: 100;
  box-shadow: 0 4px 8px rgba(34, 34, 34, 0.15);
}
.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 85vw;
  max-width: 1900px;
}
.navbar-menu-container {
  display: none;
}
.navbar-links-container a {
  margin-right: 3rem;
  text-decoration: none;
  color: var(--color-primario);
  font-size: 1.1rem;
  font-weight: 600;
}
.primary-button {
  padding: 0.9rem 1.75rem;
  background-color: var(--color-primario);
  outline: none;
  border: none;
  color: var(--color-fondo);
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  transition: 0.5s;
}
.navbar-cart-icon {
  font-size: 1.15rem;
}
.primary-button:hover {
  box-shadow: 0 4px 8px rgba(34, 34, 34, 0.5);
  background-color: rgb(234, 234, 234);
}
.navbar-menu-container svg {
  font-size: 1.5rem;
  cursor: pointer;
}

.home-banner-container {
  position: relative;
  display: flex;
  padding-top: 8rem;
}
.home-bannerImage-container {
  position: absolute;
  top: -100px;
  right: -170px;
  z-index: -2;
  max-width: 700px;
}
.home-image-section {
  max-width: 400px;
  flex: 1;
}
.home-text-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.primary-heading-header {
  font-weight: 800;
  font-size: clamp(2rem, 5vw, 4rem);
  color: var(--color-negro);
  /* line-height: 5rem; */
  max-width: 600px;
}
.primary-heading {
  font-weight: 800;
  font-size: clamp(2rem, 5vw, 4rem);
  color: var(--color-primario);
  /* line-height: 5rem; */
  max-width: 600px;
}
.primary-text-header {
  font-size: x-large;
  max-width: 600px;
  color: var(--color-text);
  margin: 1.5rem 0rem;
}
.primary-text {
  font-size: large;
  line-height: 1.5;
  color: var(--color-text-light);
  margin: 1.5rem 0rem;
}
.secondary-button {
  padding: 1rem 2.5rem;
  background-color: #fe9e0d;
  outline: none;
  border: none;
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  color: white;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.secondary-button svg {
  margin-left: 0.75rem;
  font-size: 1.5rem;
}
.secondary-button:hover {
  background-color: #e48f0f;
}
.about-section-container {
  background-color: var(--color-fondo);
  margin-top: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  align-items: stretch;
  justify-content: flex-start;
}

.about-section-image-container {
  position: relative;
  flex: 1;
  max-width: 50%;
}

.about-section-image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  box-sizing: border-box;
}
.about-section-text-container {
  margin-left: 1.5rem;
  flex: 1;
  justify-content: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 40%;
}
.primary-subheading {
  font-weight: 700;
  color: #fe9e0d;
  font-size: 1.15rem;
}
.about-buttons-container {
  margin: 2rem 0px;
  display: flex;
}
.watch-video-button {
  margin-left: 2rem;
  background-color: transparent;
  outline: none;
  border: none;
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  color: var(--color-secundario);
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}
.watch-video-button:hover {
  color: #e48f0f;
}

.watch-video-button svg {
  font-size: 3rem;
  margin-right: 1rem;
}

.section-container {
  background-color: var(--color-text-light);
  height: 100%;
}
.card-container {
  background-color: var(--color-text-light);
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
}

.card-header {
  margin: 3rem 0px;
  display: flex;
  justify-content: center;
}

.card {
  width: 300px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(34, 34, 34, 0.15);
  transition: 0.5s;
}
.card-red {
  background-color: var(--color-fondo);
}

.card-light {
  color: var(--color-primario) !important;
}
.card:hover {
  box-shadow: 0 4px 8px rgba(34, 34, 34, 0.5);
}

.card-menu-img {
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}
.card-menu-img img {
  border-radius: 10px 10px 0px 0px;
  max-width: 100%;
  transition: transform 0.3s;
}
.card-menu-img img:hover {
  transform: scale(1.15); /* Aumenta el tamaño al 110% */
}
.card-menu-title {
  text-align: center;
  color: var(--color-fondo);
  margin: 10px 0px;
}
.card-menu-subtitle {
  text-align: center;
  color: var(--color-fondo);
}
.card-menu-text {
  margin: 5px 0px;
  line-height: 1.5;
}
.card-menu-content {
  display: flex;
  justify-content: center;
  margin: 0px 1.5em;
  flex-direction: column;
}
.card-button {
  padding: 1rem 1.5rem;
  background-color: var(--color-secundario);
  outline: none;
  border: none;
  border-radius: 10px;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  color: white;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0px;
}

.contact-page-wrapper h1 {
  max-width: 900px !important;
}
.contact-page-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 6rem 0rem;
}
.contact-form-container {
  background-color: white;
  max-width: 700px;
  width: 100%;
  margin-top: 3rem;
  display: flex;
  align-items: center;
  padding: 1rem;
  border-radius: 5rem;
}
.contact-form-container input {
  flex: 1;
  height: 100%;
  border: none;
  outline: none;
  font-size: 1.3rem;
  padding: 0.5rem 1rem;
}
.footer-wrapper {
  margin: 6rem 0rem;
  display: flex;
  margin-top: 10rem;
}
.footer-logo-container {
  max-width: 110px;
}
.footer-icons {
  margin-top: 2.5rem;
}
.footer-icons svg {
  font-size: 1.5rem;
  margin-right: 1.25rem;
  color: #585858;
}
/* .footer-section-one {
  border: 2px solid blue;
} */
.footer-section-two {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}
.footer-section-columns {
  display: flex;
  flex-direction: column;
  min-width: 190px;
}
.footer-section-columns span {
  margin: 0.25rem 0rem;
  font-weight: 600;
  color: #4c4c4c;
  cursor: pointer;
}
.testimonial-section-bottom {
  margin-top: 2rem;
  background-color: white;
  padding: 1.5rem 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 750px;
  border-radius: 2rem;
  flex-direction: column;
  text-align: center;
}
.testimonial-section-bottom {
  margin: 2rem auto;
}
.testimonial-section-bottom p {
  font-weight: 700;
  color: #515151;
  max-width: 500px;
  font-size: 1.1rem;
  margin: 2rem 0rem;
}
.testimonials-stars-container svg {
  margin: 0rem 0.25rem;
  font-size: 1.5rem;
  color: #fe9e0d;
}
.testimonial-section-bottom h2 {
  margin-top: 1.5rem;
}
.work-section-wrapper {
  margin-top: 15rem;
}
.work-section-top p {
  text-align: center;
  max-width: 600px !important;
}
.work-section-top h1 {
  max-width: 700px !important;
}
.work-section-top {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.work-section-info {
  width: 290px;
  min-height: 350px;
  background-color: white;
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 1rem;
  color: #505050;
  margin: 1rem 2rem;
}
.work-section-info h2 {
  margin: 1rem 0rem;
}
.work-section-bottom {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.work-section-info p {
  flex: 1;
  display: flex;
  align-items: center;
  font-weight: 600;
}

@media (max-width: 1000px) {
  .nav-logo {
    max-width: 80px;
    border-radius: 10px;
  }
  .navbar-links-container a {
    margin-right: 1rem;
    font-size: 1rem;
  }
  .primary-button {
    font-size: 1rem;
  }
  .home-bannerImage-container {
    max-width: 600px;
  }
}
@media (max-width: 800px) {
  .card-container {
    flex-direction: column;
  }

  .card {
    margin-bottom: 3em;
  }
  .nav-logo {
    max-width: 80px;
    border-radius: 10px;
  }
  .nav-logo-container {
    max-width: 140px;
  }
  .navbar-links-container {
    display: none;
  }
  .navbar-menu-container {
    display: flex;
  }
  .home-bannerImage-container {
    max-width: 450px;
  }
  .home-banner-container,
  .about-section-container {
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
  .about-section-container {
    flex-direction: column;
  }
  .home-image-section {
    width: 100%;
    max-width: 400px;
  }

  .about-section-image-container {
    max-width: 100%;
    width: auto;
  }
  .primary-heading {
    text-align: center;
    max-width: 90%;
  }
  .primary-text {
    font-size: medium;
    text-align: center;
    max-width: 80%;
  }
  .primary-text-header {
    font-size: medium;
    text-align: center;
    max-width: 100%;
  }

  .home-text-section {
    justify-content: center;
    align-items: center;
    margin-top: 3rem;
  }
  .about-section-text-container {
    max-width: 80%;
    margin-left: 0px;
    justify-content: center;
    align-items: center;
    margin-top: 3rem;
  }
  .secondary-button {
    font-size: 1rem;
    padding: 0.8rem 2rem;
    cursor: pointer;
  }

  .about-buttons-container {
    justify-content: center;
    flex-direction: column;
  }
  .primary-subheading {
    text-align: center;
  }
  .watch-video-button {
    margin-left: 0rem !important;
    margin-top: 1rem;
    font-size: 1rem !important;
  }
  .watch-video-button svg {
    margin-right: 0.5rem !important;
  }
  .about-section-image-container {
    margin-right: 0rem !important;
  }
  .work-section-wrapper {
    margin-top: 5rem !important;
  }
  .work-section-bottom {
    margin-top: 1rem !important;
  }
  .contact-form-container {
    padding: 0.5rem !important;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 1rem !important;
  }
  .contact-form-container input {
    font-size: 1rem !important;
    max-width: 100%;
    padding: 0.5rem 1rem !important;
    margin-bottom: 0.8rem !important;
    text-align: center;
  }
  .footer-wrapper {
    flex-direction: column;
  }
  .footer-section-two {
    flex-direction: column;
    margin-top: 2rem;
  }
  .footer-section-columns {
    margin: 1rem 0rem;
  }
  .App {
    max-width: 95vw;
  }
}
@media (max-width: 600px) {
  .nav-logo {
    max-width: 80px;
    border-radius: 10px;
  }
  .home-bannerImage-container,
  .about-background-image-container {
    display: none;
  }
}
